import { Component, OnInit } from '@angular/core';
import { BasePage } from '../base/base.page';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { Store } from '@ngxs/store';
import { UtilsService } from '../@core/utils.service';
import { Constants } from '../@global/constants';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { CommonState } from '../@store/common/common.state';
import { StorageService } from '../@core/storage.service';

@Component({
  selector: 'app-legal-data-options',
  templateUrl: './legal-data-options.page.html',
  styleUrls: ['./legal-data-options.page.scss'],
})
export class LegalDataOptionsPage extends BasePage implements OnInit {
  private language;
  constructor(
    router: Router,
    location: Location,
    store: Store,
    utilsService: UtilsService,
    private iab: InAppBrowser,
    private storage: StorageService
  ) {
    super(location, router, store, utilsService);
  }

  async ngOnInit() {
    this.language = this.store.selectSnapshot(CommonState.getLanguage) || (await this.storage.getLanguage());
  }

  public openPrivacyPolicy() {
    this.iab.create(Constants.PRIVACY_POLICY_URL + '?lang=' + this.language);
  }

  public openConditions() {
    this.iab.create(Constants.USE_TERMS_AND_CONDITIONS_URL + '?lang=' + this.language);
  }
}
